//PARAMS
@import "../../scss/params.scss";

/* CONTACT *////////////////////////////////////
.who-i-m-component {
	overflow-x: auto;
	height: 100vh;

	ul {
		margin-right: 40px;
		margin-bottom: 20px;
		li{
			a{
		    color: $brancoOn;
		    text-align: right;
		    display: block;
		    font-weight: 200;
		    text-decoration: none;
		    line-height: 25px;
		    &:hover {
		    	opacity: 0.5;
		    }
			}
		}
	}
}

